import { yupResolver } from "@hookform/resolvers/yup";
import dayjs from "dayjs";
import { FC, useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import * as yup from "yup";
import { useAppDispatch, useAppSelector } from "~app/store/hooks";
import {
  closeEditCoachPopup,
  openConfirmCoachPopup,
} from "~features/popup/popupSlice";
import { editCoach, setChosenCoach, TCoach } from "~entities/coach/coachSlice";
import { editGuest, TGuest } from "~entities/guest/guestSlice";
import { updateGuestToSkipass } from "~entities/skipass/skipassSlice";
import { DateInput } from "~shared/date-input";
import { FormBox } from "~shared/form-box";
import { ImageInput } from "~shared/image-input";
import { ModalButton } from "~shared/modal-button";
import { TextInput } from "~shared/text-input";

export type TFormData = {
  photo?: string;
  name: string;
  surname: string;
  birthDate: string;
  gender?: string;
  sport: string;
};

const schema = yup
  .object({
    photo: yup.string(),
    name: yup.string().min(2, "  2 ").required(),
    surname: yup.string().min(2, "  2 ").required(),
    birthDate: yup.string().required(),
    gender: yup.string().matches(/(^$|^$)/, `  ""  ""`),
    sport: yup.string().required(),
  })
  .required();

export const EditCoach: FC = () => {
  const [loading, setLoading] = useState<boolean>(false);

  const coach = useAppSelector((state) => state.coaches.chosenCoach) as TCoach;

  const dispatch = useAppDispatch();

  const methods = useForm<TFormData>({
    mode: "onChange",
    reValidateMode: "onChange",
    resolver: yupResolver(schema),
    defaultValues: {
      // photo: coach.photo,
      name: coach.name,
      surname: coach?.surname,
      birthDate: dayjs(coach.birthDate, "DD.MM.YYYY") as unknown as string,
      gender: coach.sex,
      sport: coach.category,
    },
  });

  const { handleSubmit, formState } = methods;

  const updateGuest = (guest: TGuest) => {
    return dispatch(
      editGuest({
        guestId: guest.id as string,
        data: {
          ...guest,
          coachNameSurname: `${coach?.name} ${coach?.surname}`,
          coachSex: coach.sex,
          coachCategory: coach.category,
        },
      }),
    )
      .unwrap()
      .then((guest) => {
        guest.skiPassId &&
          dispatch(
            updateGuestToSkipass({
              guestId: guest.id,
              skipassId: guest.skiPassId,
            }),
          );
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const updateGuests = () => {
    if (coach.guests.length > 0) {
      coach.guests.forEach((guest) => {
        return updateGuest(guest);
      });
    }
  };

  const updateCoach = (data: TFormData, birthDate: string) => {
    const newData = {
      photo: data.photo,
      name: data.name,
      surname: data.surname,
      birthDate: birthDate,
      gender: data.gender,
      category: data.sport,
      guests: coach.guests,
    };
    return dispatch(
      editCoach({
        coachId: coach.id as string,
        data: newData,
      }),
    )
      .unwrap()
      .then((coach) => {
        dispatch(closeEditCoachPopup());
        updateGuests();
        coach && dispatch(setChosenCoach(coach));
      })
      .then(() => {
        dispatch(openConfirmCoachPopup());
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  function onSubmit(data: TFormData) {
    setLoading(true);
    const birthDate = dayjs(data.birthDate).format("DD.MM.YYYY");

    updateCoach(data, birthDate);
  }

  return (
    <FormProvider {...methods}>
      <FormBox onSubmit={handleSubmit(onSubmit)}>
        <ImageInput name="photo" />
        <TextInput name="name" label="" />
        <TextInput name="surname" label="" />
        <DateInput name="birthDate" label=" " />
        <TextInput
          name="gender"
          label=""
          disabled={formState.defaultValues?.gender !== undefined}
        />
        <TextInput name="sport" label=" " />
        <ModalButton
          btnText=""
          disabled={!formState.isValid || loading || !formState.isDirty}
        />
      </FormBox>
    </FormProvider>
  );
};
